<template>
    <section>
        <el-card style="margin-bottom: 20px;">
            <!-- ---------------------------------------------------------------  ------------------------------------------------ -->
            <el-row :gutter="20" style="margin-bottom: 20px;">
                <el-form :model="searchForm" :inline="true" class="demo-form-inline" label-width="110px" ref="searchRef">
                    <el-form-item prop="time">
                        <el-date-picker
                            v-model="searchForm.time"
                            type="daterange"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            value-format="timestamp"
                            unlink-panels
                        >
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="" prop="keyword">
                        <el-input placeholder="" v-model="searchForm.keyword" clearable></el-input>
                    </el-form-item>
                    <el-checkbox v-model="searchForm.checked"></el-checkbox>
                    <el-button type="primary" @click="search"></el-button>
                    <el-button @click="reset('searchRef')"></el-button>
                </el-form>
            </el-row>
        </el-card>

        <el-card>
            <!-----------------------------------------------------------------  ------------------------------------------------ -->
            <el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="20">
                        <div></div>
                    </el-col>
                    <el-col :span="2">
                        <el-button type="success" @click="showAddFormDialogVisible = true"></el-button>
                    </el-col>
                </el-row>
                <el-table :data="stockList" border style="width: 100% " :row-class-name="tableRowClassName">
                    <el-table-column prop="Provider" label="" align="center"></el-table-column>
                    <el-table-column prop="Date" label="" align="center">
                        <template slot-scope="scope">
                            <div>{{ scope.row.Date | formatDate }}</div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="Category" label="" align="center"> </el-table-column>
                    <el-table-column prop="Brand" label="" align="center"></el-table-column>
                    <el-table-column prop="Model" label="" align="center"></el-table-column>
                    <el-table-column prop="Price" label="()" align="center"></el-table-column>
                    <el-table-column prop="Quantity" label="()" align="center"> </el-table-column>
                    <el-table-column prop="Inventory" label="()" align="center"> </el-table-column>
                    <el-table-column prop="Remarks" label="" width="200px" align="center" class="remark"> </el-table-column>
                    <!--  -->
                    <el-table-column width="300px" scope label="" align="center">
                        <template slot-scope="scope">
                            <!--  -->
                            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                                <el-button icon="el-icon-setting" size="small" type="primary" @click="showDditForm(scope.row)"></el-button>
                            </el-tooltip>
                            <!--  -->
                            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                                <el-button icon="el-icon-edit" size="small" type="success" @click="showRemark(scope.row)"></el-button>
                            </el-tooltip>
                            <!--  -->
                            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                                <el-button icon="el-icon-s-promotion" size="small" type="warning" @click="showOutStock(scope.row)"></el-button>
                            </el-tooltip>

                            <!--  -->
                            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                                <el-button icon="el-icon-delete-solid" size="small" type="danger" @click="deleteStock(scope.row.Id)"></el-button>
                            </el-tooltip>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
        </el-card>

        <!---------------------------------------------------------------------------------------------------------------->
        <el-dialog
            title=""
            :visible.sync="showAddFormDialogVisible"
            width="30%"
            :rules="addFormRules"
            @close="resetForm('addForm')"
        >
            <el-form ref="addForm" :model="addForm" label-width="120px" :rules="addFormRules">
                <el-form-item label="" prop="Provider">
                    <el-input v-model="addForm.Provider"></el-input>
                </el-form-item>
                <el-form-item label="" prop="Date">
                    <el-date-picker v-model="addForm.Date" type="date" placeholder="" format="yyyy  MM  dd " value-format="timestamp">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="Cate">
                    <el-cascader
                        v-model="addValue"
                        :options="options"
                        :props="{ value: 'Name', label: 'Name', children: 'Category', expandTrigger: 'hover' }"
                        @change="addChangeRef"
                        ref="cascader"
                    >
                    </el-cascader>
                </el-form-item>
                <el-form-item label="" prop="Model">
                    <el-input v-model="addForm.Model"></el-input>
                </el-form-item>
                <el-form-item label="()" prop="Price">
                    <el-input v-model.number="addForm.Price" oninput="value=value.replace(/[^\d{1,}\.\d{1,}|\d{1,}]/g,'')"></el-input>
                </el-form-item>
                <!-- <el-form-item label="()" prop="Inventory">
                    <el-input-number v-model="addForm.Inventory" label="" disabled></el-input-number>
                </el-form-item> -->
                <el-form-item label="()" prop="Quantity">
                    <el-input-number v-model="addForm.Quantity" label="" :min="0" :precision="0"></el-input-number>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="showAddFormDialogVisible = false"></el-button>
                <el-button type="primary" @click="addStock"></el-button>
            </span>
        </el-dialog>

        <!---------------------------------------------------------------------------------------------------------------->
        <el-dialog title="" :visible.sync="showEditFormDialogVisible" width="30%" :rules="addFormRules" @close="resetForm('editForm')">
            <el-form ref="editForm" :model="editForm" label-width="120px" :rules="addFormRules">
                <el-form-item label="" prop="Provider">
                    <el-input v-model="editForm.Provider"></el-input>
                </el-form-item>
                <el-form-item label="" prop="Date">
                    <el-date-picker v-model="editForm.Date" type="date" placeholder="" format="yyyy  MM  dd " value-format="timestamp">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-cascader
                        v-model="editValue"
                        :options="options"
                        :props="{ value: 'Name', label: 'Name', children: 'Category', expandTrigger: 'hover' }"
                        @change="editChangeRef"
                        ref="editCascader"
                    >
                    </el-cascader>
                </el-form-item>
                <el-form-item label="" prop="Model">
                    <el-input v-model="editForm.Model"></el-input>
                </el-form-item>
                <el-form-item label="()" prop="Price">
                    <el-input v-model="editForm.Price" oninput="value=value.replace(/[^\d{1,}\.\d{1,}|\d{1,}]/g,'')" @change="changePrice"></el-input>
                </el-form-item>
                <el-form-item label="()" prop="Quantity">
                    <el-input-number v-model="editForm.Quantity" label="" :min="0" :precision="0"></el-input-number>
                </el-form-item>
                <el-form-item label="()" prop="EditNum">
                    <el-input v-model="editForm.EditNum" label="" disabled></el-input>
                    <!-- <div>{{editForm.Quantity - editForm.Inventory}}</div> -->
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="showEditFormDialogVisible = false"></el-button>
                <el-button type="primary" @click="editFormHandler"></el-button>
            </span>
        </el-dialog>

        <!---------------------------------------------------------------------------------------------------------------->
        <el-dialog title="" :visible.sync="showRemarkDialogVisible" width="30%" :rules="addFormRules" @close="resetForm('remarkForm')">
            <el-form ref="remarkForm" :model="remarkForm" label-width="120px" :rules="addFormRules">
                <el-form-item label=" : " prop="Remarks">
                    <el-input type="textarea" autosize v-model="remarkForm.Remarks" label=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="showRemarkDialogVisible = false"></el-button>
                <el-button type="primary" @click="addRemark"></el-button>
            </span>
        </el-dialog>

        <!---------------------------------------------------------------------------------------------------------------->
        <el-dialog title="" :visible.sync="outStockFormDialogVisible" width="30%" :rules="addFormRules" @close="resetForm('outStockForm')">
            <el-form ref="outStockForm" :model="outStockForm" label-width="120px" :rules="addFormRules">
                <el-form-item label="" prop="provider">
                    <el-input v-model="outStockForm.provider" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="outStockValue" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="model">
                    <el-input v-model="outStockForm.model" disabled></el-input>
                </el-form-item>
                <el-form-item label="()" prop="inventory">
                    <el-input v-model="outStockForm.inventory" disabled></el-input>
                </el-form-item>
                <el-form-item label="()" prop="price">
                    <el-input v-model="outStockForm.price" disabled></el-input>
                </el-form-item>

                <el-form-item label="" prop="shipper">
                    <el-input v-model="outStockForm.shipper" label=""></el-input>
                </el-form-item>

                <el-form-item label="" prop="outDate">
                    <el-date-picker
                        v-model="outStockForm.outDate"
                        type="date"
                        placeholder=""
                        format="yyyy  MM  dd "
                        value-format="timestamp"
                    >
                    </el-date-picker>
                </el-form-item>

                <el-form-item label="()" prop="quantity">
                    <el-input-number v-model="outStockForm.quantity" label="" :min="0" :precision="0"></el-input-number>
                </el-form-item>

                <el-form-item label="()" prop="sell">
                    <el-input v-model="outStockForm.sell" label="" oninput="value=value.replace(/[^\d{1,}\.\d{1,}|\d{1,}]/g,'')"> </el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="outStockFormDialogVisible = false"></el-button>
                <el-button type="primary" @click="outStock"></el-button>
            </span>
        </el-dialog>
    </section>
</template>
<script>
import formatDate from '../../api/formatDate.js'
import datas from './datas.js'
import methods from './methdos.js'

export default {
    data() {
        return datas.init()
    },
    created() {
        this.getList(), this.getCateList()
    },
    methods: methods,
    filters: {
        formatDate: function(value) {
            // 
            if (value == null) {
                return ''
            } else {
                let date = new Date(value)
                let y = date.getFullYear() // 
                let MM = date.getMonth() + 1 // 
                MM = MM < 10 ? '0' + MM : MM
                let d = date.getDate() // 
                d = d < 10 ? '0' + d : d
                return y + '-' + MM + '-' + d
            }
        }
    }
}
</script>

<style scoped>
.el-table {
    margin-top: 15px;
}
.el-col {
    margin: 0 20px;
}
.el-button {
    margin: 0 6px;
}

.coll {
    position: relative;
}

.el-table /deep/ .over {
    background-color: #ffd700;
}

.el-table /deep/ .cell {
    white-space: pre-wrap;
}
</style>



