export default {
    init: () => {
        return {
            // 
            customerList: [],

            //  
            showRemarkFormDialogVisible: false,
            //  
            remarkForm: {
                Remarks: '',
                Id: ''
            },
            // 
            searchForm: {
                shipper: '',
                // 
                time: [{
                    begin: 0,
                    end: 0
                }],
                checked: false
            },
            //  
            customerFormDialogVisible: false,
            // 
            setCustomerForm: {
                shipper: '',
                model: '',
                name: '',
                phone: '',
                address: '',
                saleDate: '',
                deliveryDate: '',
                remarks: '',
                status: 1
            },

            // ,
            editCustomerFormDialogVisible : false,
            // 
            editCustomerForm: {
                id:'',
                shipper: '',
                model: '',
                name: '',
                phone: '',
                address: '',
                saleDate: '',
                deliveryDate: '',
                remarks: '',
                status: 1
            },

            // 
            formRules : {
                shipper : [
                    {required : true, message : '', trigger : 'blur'}
                ],
                model : [
                    {required : true, message : '', trigger : 'blur'}
                ],
                name : [
                    {required : true, message : '', trigger : 'blur'}
                ],
                phone : [
                    {required : true, message : '', trigger : 'blur'}
                ],
                address : [
                    {required : true, message : '', trigger : 'blur'}
                ],
                saleDate : [
                    {required : true, message : '', trigger : 'blur'}
                ],
                deliveryDate : [
                    {required : true, message : '', trigger : 'blur'}
                ],
            }
        }
    }
}



