export default {
    getList() {
        // debugger
        this.$axios.get('http://localhost/category').then(res => {
            this.list = res.data

            for (let i = 0; i < res.data.length; i++) {
                for (let j = 0; j < res.data[i].Category.length; j++) {
                    let obj = {
                        category: res.data[i].Name,
                        brand: res.data[i].Category[j].Name
                    }
                    window.sessionStorage.setItem('key_' + res.data[i].Category[j].Id, JSON.stringify(obj));
                }
            }
        })
    },
    //
    addcate() {
        for( var i = 0; i< this.list.length; i++) {
           if (this.list[i].Name == this.addForm.name){
            this.$notify.error({
                title: '',
                message: '',
                position: 'bottom-right'
            });
            return true
           }
        }

        this.$axios.post('http://localhost/category', {
            name: this.addForm.name
        }).then(res => {
            this.$notify.success({
                title: '',
                message: '',
                position: 'bottom-right'
            });
            window.sessionStorage.clear();
            this.getList()
            // let _this = this;
            // setTimeout(() => {
            //     _this.getList()
            // }, 100)

        }).catch(err => {
            this.$notify.error({
                title: '',
                message: '',
                position: 'bottom-right'
            });
        });

        this.$refs.addCateRef.resetFields()
        this.visible = false
    },
    // 
    addFormClose() {
        this.$refs.addCateRef.resetFields()
    },

    //Id
    addBrandId(id) {
        this.bId = id
        this.showBrandDialogVisible = true
    },
    //
    addBrand() {
        this.$axios.post('http://localhost/category', {
            'pid': this.bId,
            'name': this.addBrandsForm.name
        }).then(() => {
            this.$notify.success({
                title: '',
                message: '',
                position: 'bottom-right'
            });
            window.sessionStorage.clear();
            this.getList()
        }).catch( err => {
            this.$notify.error({
                title: '',
                message: '',
                position: 'bottom-right'
            });
        })
        this.showBrandDialogVisible = false
        this.$refs.addBrandsFormRef.resetFields()
    },
    addBrandsFormClose() {
        this.$refs.addBrandsFormRef.resetFields()
    },
    // 
    deleteBrand(id) {
        this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).then(() => {
            this.$axios.delete('http://localhost/category/' + id).then(res => {
                window.sessionStorage.clear();
                this.getList()
                this.$notify.success({
                    title: '',
                    message: '',
                    position: 'bottom-right'
                });
                // this.$message({
                //     type: 'success',
                //     message: '!'
                // });
            }).catch(err => {
                // console.log(req);
                this.$notify.error({
                    title: '',
                    message: '',
                    position: 'bottom-right'
                });
                // this.$message({
                //     type: 'info',
                //     message: ''
                // });
            });
        })
    },

    // 
    removeBrand(id) {
        this.$axios.delete('http://localhost/category/' + id).then(
            res => {
                window.sessionStorage.clear();
                this.getList(),
                this.$notify.success({
                    title: '',
                    message: '',
                    position: 'bottom-right'
                });
                    // this.$message({
                    //     type: 'success',
                    //     message: ''
                    // })
            }
        ).catch(
            err => {
                this.$notify.error({
                    title: '',
                    message: '',
                    position: 'bottom-right'
                });
                // this.$message({
                //     type: 'info',
                //     message: ''
                // })
            }
        )
    },



}



